import React, {Component} from "react";
import SimpleModalButton from "../../components/simple-modal-button";
import {stopJob} from "../../api";
import {isEmptyObject} from "../../utils/HtmlUtils";

export default class StopJob extends Component {
    stopJob = async () => {
        await stopJob({name: this.props.selectedRow.name}).then(() => this.props.onSuccess())
    }

    render() {
        const { selectedRow={} } = this.props
        const { name, status} = selectedRow;
        return (
            <SimpleModalButton buttonStyle={{marginLeft: "8px"}} title="停止Job" onOk={this.stopJob} disabled={isEmptyObject(selectedRow) || status === -1}>
                <span>
                    确定停止Job：{name}
                </span>
            </SimpleModalButton>
        )
    }
}