/*
应用中所有接口请求模块
    |- 每个接口返回的都是promise对象
 */

import {del, get, post} from "./ajax"

// 登录
export const isLogin = () => get('/user/isLogin')
export const reqLogin = (userName, password) => post('/user/login', { userName, password })
export const reqLoginOut = () => post('/user/loginOut')

// 获取资源列表
export const getResources = (data) => get('/resources', data)

// Job列表
export const listJob = (data) => get('/job/list', data)
export const startJob = (data) => get(`/job/start`, data)
export const stopJob = (data) => get(`/job/stop`, data)
export const runJob = (data) => get(`/job/run`, data)
export const addJob = (data) => get(`/job/add`, data)
export const editJob = (data) => get(`/job/edit`, data)
