import React, {Component} from "react";
import SimpleModalButton from "../../components/simple-modal-button";
import {startJob} from "../../api";
import {isEmptyObject} from "../../utils/HtmlUtils";

export default class StartJob extends Component {
    startJob = async () => {
        await startJob({name: this.props.selectedRow.name}).then(() => this.props.onSuccess())
    }

    render() {
        const { selectedRow={} } = this.props
        const { name, status} = selectedRow;
        return (
            <SimpleModalButton buttonStyle={{marginLeft: "8px"}} title="运行Job" onOk={this.startJob} disabled={isEmptyObject(selectedRow) || status === 1}>
                <span>
                    确定运行Job：{name}
                </span>
            </SimpleModalButton>
        )
    }
}