import React, {Component} from "react";
import DefaultModalButton from "../../components/default-modal-button/DefaultModalButton";
import {editJob} from "../../api";
import {isEmptyObject} from "../../utils/HtmlUtils";

export default class EditJob extends Component {
    render() {
        const { selectedRow={} } = this.props
        return (
            <DefaultModalButton
                {...this.props}
                title="编辑Job"
                value='编辑Job'
                formConfig={this.formConfig}
                updateApi={editJob}
                disabled={isEmptyObject(selectedRow)}
            />
        )
    }

    formConfig = [
        {key: 'name', hidden: true},
        {label: 'Job标题', key: 'title', type: 'input'},
        {label: 'Job时钟*', key: 'cron', type: 'input'},
    ];
}