/*
发送异步ajax请求函数模块
    |- 封装axios库
    |- 函数返回promise对象
 */

import axios from 'axios'
// import {hashHistory} from "react-router-dom"


import {message} from "antd"
import  Qs from 'qs';
import {isNull} from "../utils/HtmlUtils";

const service = axios.create({
    baseURL: '/api/'
})

export function get(url,data={}) {
    return handleGet(service.get(url, {
        params: data,
        headers:{"Content-Type": "application/json"},
        paramsSerializer: function(params) {
            return Qs.stringify(params, {arrayFormat: 'repeat'})
        },
    }))
}

export function post(url, data = {}) {
    return handlePost(service.post(url, data, {
        headers:{"Content-Type": "application/json"}
    }))
}

export function del(url, data = {}) {
    return handlePost(service.delete(url, data, {
        headers:{"Content-Type": "application/json"}
    }))
}

export function patch(url, data = {}) {
    return handlePost(service.patch(url, data, {
        headers:{"Content-Type": "application/json"}
    }))
}
function handleGet(promise) {
    return new Promise((resolve, reject) => {
        promise.then(req => {
            if (isNull(req.data)) {
                message.error( "请求失败")
                reject(req)
            }
            const data = req.data;
            if (data.success) {
                resolve(data.data)
            } else if (data.message === "请重新登录") {
                window.location.reload();
            } else {
                message.error(isNull(data.message, "请求失败"));
                reject(req)
            }
        }).catch(reason => {
            message.error(isNull(reason.message, "请求异常2"));
            reject(reason)
        });
    })
}

function handlePost(promise) {
    return new Promise((resolve, reject) => {
        promise.then(req => {
            if (isNull(req.data)) {
                message.error( "请求异常")
                reject(req)
            }
            const data = req.data;
            if (data.success) {
                message.success(isNull(data.message, "请求成功"))
                resolve(data.data)
            } else if (data.message === "请重新登录") {
                window.location.reload();
            } else {
                message.error(isNull(data.message, "请求失败"));
                reject(req)
            }
        }).catch(reason => {
            message.error(isNull(reason.message, "请求异常"));
            reject(reason)
        });
    })
}
