import React, {Component} from "react";
import {listJob} from "../../api";
import DefaultTable from "../../components/default-table";
import {idRowKey} from "../../utils/HtmlUtils";
import AddJob from "./AddJob";
import StartJob from "./StartJob";
import RunJob from "./RunJob";
import StopJob from "./StopJob";
import EditJob from "./EditJob";

export default class VideoInfoManager extends Component {

    columnsConfig = [
        {title: 'Job标题', key: 'title', width: 300, type: 'search', ellipsis: true},
        {title: 'Job名称', key: 'name', width: 300, type: 'search', ellipsis: true},
        {title: 'Job时钟', key: 'cron', width: 300, type: 'search', ellipsis: true},
        {title: '状态', key: 'status', width: 100, type: 'choose', chooseMap: 'statusList'},
    ];

    handleButtonsInit = (that) => {
        const { state, handleUpdated } = that
        const { selectedRows } = state
        const selectedRow = selectedRows[0] || {}
        return (
            <>
                <AddJob onSuccess={handleUpdated}/>
                <EditJob selectedRow={selectedRow} onSuccess={handleUpdated}/>
                <StartJob selectedRow={selectedRow} onSuccess={handleUpdated}/>
                <RunJob selectedRow={selectedRow} onSuccess={handleUpdated}/>
                <StopJob selectedRow={selectedRow} onSuccess={handleUpdated}/>
            </>
        )
    }

    handleResourcesInit = () => ({
        statusList: [{value: 0, text: '就绪', tag: 'blue'}, {value: 1, text: '启用', tag: 'green'}, {value: -1, text: '停用', tag: 'red'}],
    })

    render() {
        return (
            <DefaultTable
                isShowRowSelection={true}
                needResourcesList={[]}
                defaultPageSize={20}
                rowKey={idRowKey}
                columnsConfig={this.columnsConfig}
                getDataApi={listJob}
                onButtonsInit={this.handleButtonsInit}
                onResourcesInit={this.handleResourcesInit}
            />
        )
    }
}