import React, {Component} from "react";
import SimpleModalButton from "../../components/simple-modal-button";
import {runJob} from "../../api";
import {isEmptyObject} from "../../utils/HtmlUtils";

export default class RunJob extends Component {
    runJob = async () => {
        await runJob({name: this.props.selectedRow.name}).then(() => this.props.onSuccess())
    }

    render() {
        const { selectedRow={} } = this.props
        const { name } = selectedRow;
        return (
            <SimpleModalButton buttonStyle={{marginLeft: "8px"}} title="运行一次" onOk={this.runJob} disabled={isEmptyObject(selectedRow)}>
                <span>
                    确定运行一次：{name}
                </span>
            </SimpleModalButton>
        )
    }
}